@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
html{box-sizing:border-box}*,*:before,*:after{box-sizing:inherit}body{margin:0;padding:0;line-height:1;font-family:"Roboto", sans-serif;color:black;background-color:#fafafa}.header{border-bottom:solid 1px #ca2100;background-color:#db4c3f;box-shadow:0 1px 2px rgba(0,0,0,0.15);height:44px;width:100%}.header img{width:24px}.header nav{height:100%;max-width:922px;display:flex;align-items:center;margin:auto}.content{display:flex;max-width:922px;margin:auto}.sidebar{-webkit-user-select:none;-ms-user-select:none;user-select:none;width:266px;height:100vh;padding-top:74px;border-right:1px solid #f1f1f1;background-color:#fafafa}.sidebar div{margin-bottom:10px;cursor:pointer;padding:15px}.sidebar div.active{background-color:white}.sidebar div .icon{margin-right:10px}.task{background-color:#ec1a1a;height:100vh;width:calc(922px - 266px);padding:74px 20px 0}.task .add-task-btn{cursor:pointer}.task .add-task-btn .plus{color:#dd4b39;font-size:18px}.task .add-task-btn .add-task-text{color:#545454;font-size:14px;margin-left:15px}.task .add-task-dialog{margin-top:10px}.task .add-task-dialog input{width:100%;border:1px solid white;background-color:#f11313;border-radius:3px;height:35px;padding-left:10px;padding-right:10px}.task .add-task-dialog .add-task-actions-container{margin-top:10px;display:flex;justify-content:space-between}.task .add-task-dialog .add-task-actions-container .add-btn{background-color:#db4c13;color:white;border:1px solid transparent;margin-right:5px;font-weight:bold;font-size:13px;padding:6px 12px 7px 12px;border-radius:3px !important}.task .add-task-dialog .add-task-actions-container .add-btn:disabled{background-color:grey}.task .add-task-dialog .add-task-actions-container .add-btn:active{-webkit-transform:scale(0.9);transform:scale(0.9)}.task .add-task-dialog .add-task-actions-container .cancel-btn{all:unset;color:#545454;cursor:pointer;font-size:14px;margin:0px 5px}.task .task-item-container{margin-top:40px;padding:0 20px}.task .task-item-container .task-item{display:flex;justify-content:space-between}.task .task-item-container .task-item:not(:last-child){border-bottom:1px solid red}

